@import url('https://fonts.googleapis.com/css2?family=Yeseva+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Piedra&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@500&display=swap');
*{
  margin:none;
  padding:none;
}
.affiliate-text{
  margin:0px 100px 0px 100px;
}
.body{
 background-color:#b19cd9;
  margin-bottom: 0px;
}
.flavor-title
{
  font-family: 'Source Sans Pro', sans-serif;
  font-size:30px;
}
html{
   padding:0px;
  background-color:red;
}
.rage{
  font-family: 'Russo One', sans-serif;
  font-size:40px;
  margin-top:40px;
  margin-bottom:40px;
}

li{
padding-top:3rem;
text-decoration: none;
list-style-type:none;
}
Nav.Link{
  width: fit-content;
}
#flavor_container{
  margin:0px;
  padding:0px;
}

.video{
  background-color:black;
  opacity:0.5;
 
}
.About-customers{
  font-family: 'Fira Sans', sans-serif;
}
.title{
 animation:type 3s;
 font-size: 60px;
 font-family: 'Abril Fatface', cursive;
}
@keyframes type{
  0%{
    padding-bottom:10vh;
  }
  100%{
    padding-bottom:45vh;
  }
}
.title{
  padding-bottom:45vh;
}
.text-heading
{
  background-color: none;
  font-size: 40px;
  font-family: 'Piedra', cursive;
  color: #FF69B4 ;
  text-align:center;
}
.grid_div
{
  margin:10px;
}
.grid-text{
  text-align:right;
}
.About-header{
  margin:50px 200px 50px 200px;
}
.About-text{
  margin-right: 200px;
}
.About-text-left{
  margin-left: 200px;
}
.navBrand{
  position:absolute;
 margin-left:45%;
  
}
.flex-column{
  padding-left:50px;
}

@media only screen and (max-width: 1000px){
  .flex-column{
    padding-left:10px;
  }
  .navBrand{
    position: relative;
    margin-left: 40%;
  }
  .affiliate-text{
    padding:0px;
  }
  .grid{
    width:100%;
  }
  .About-text{
    margin:0;
  }
  .About-text-left{
    margin:0;
  }
  .About-header{
    
    margin: 0;
  }
  .grid-text{
    text-align: center;
  }
  .player{
   height:100%;
   width:100%;
  }
  .slide{
    height:fit-content;
  }
  .title{
    display:none;
  }
  .grid-row{
  padding:12vw;
  text-align:center;
  }
}
.Image{
   border:5px solid white;
   padding: 10px;
  border-radius:100% ;
  height:540px;
  width:500px;
  object-fit: cover;
  margin-left:1vw;
  margin-top:5vh;
}
.rage_image{
  margin-left:20%;
}
@media only screen and (max-width:768px){
  .rage_image{
    margin:0;
  }
  .Image{
    display:none;
  }
  .rage{
    margin:none;
    padding:none;
  }
}
.grid-top{
  padding-top:30px;
  padding-bottom:30px;

transition: scale,0.3s;
}

.grid{
  height:500px;
  object-fit: cover;
  padding:none;
  transition: scale,0.3s;
  transform: scale(1.1);
  margin:none;
}
.grid:hover{
 transform: scale(1.2);
 box-shadow: white;
 overflow: hidden;
}
.col-grid{
  padding-left:0px;
  padding-right:0px;
  overflow: hidden;
}

.img{
  width:fit-content;
}
.text
{ 
font-family: 'Yeseva One', cursive;  
text-align:center;
color: #FF69B4 ;
}
